body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

input, button {
  padding: 8px;
  font-size: 16px;
  width: 250px;
  margin-bottom: 10px;
}

button {
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  width: 100px;
}

button:hover {
  background-color: #45a049;
}
